import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';
import { toJS } from 'mobx';
import { message, Form, Input, Radio, Select } from 'antd';
import GlobalModal from 'Components/GlobalModal';
@inject('SystemStore')
@observer
class Department extends Component {
    render() {
        const { modalVisible, formData, columnsType, arr, isReview } = this.props.SystemStore
        const formLabelCol = { span: 4 }
        const formWrapperCol = { span: 18 }
        const { getFieldDecorator } = this.props.form;
        const { Option } = Select
        console.log(arr)
        return <div>
            {
                modalVisible && <GlobalModal
                    title={JSON.stringify(formData) === '{}' ? '' : ''}
                    visible={modalVisible}
                    width={'50%'}
                    onOk={this.handleSubmit}
                    onCancel={e => this.props.SystemStore.modalChange(0)}
                    children={
                        <div>
                            <Form onSubmit={this.handleSubmit} layout='horizontal' labelCol={formLabelCol} wrapperCol={formWrapperCol}>
                                {columnsType === '1' ? <div>
                                    <Form.Item label="">
                                        {getFieldDecorator('parentId', {
                                            initialValue: formData.parentId || ''
                                        })(
                                            <Select disabled>
                                                {arr.map(item => (<Option value={item.id} key={item.id}>{item.name}</Option>))}
                                            </Select>
                                        )}
                                    </Form.Item>
                                    <Form.Item label="">
                                        {getFieldDecorator('name', {
                                            rules: [{ required: true, message: '' }],
                                            initialValue: formData.name || ''
                                        })(
                                            <Input placeholder="" />
                                        )}
                                    </Form.Item>
                                    <Form.Item label="">
                                        {getFieldDecorator('delFlag', {
                                            rules: [{ required: false, message: '' }],
                                            initialValue: formData.delFlag || 0
                                        })(
                                            <Radio.Group>
                                                <Radio value={1}></Radio>
                                                <Radio value={0}></Radio>
                                            </Radio.Group>
                                        )}
                                    </Form.Item>
                                </div> : <div>
                                        {<Form.Item label="">
                                            {getFieldDecorator('customerCode', {
                                                rules: [{ required: true, message: '' }],
                                                initialValue: formData.customerCode || ''
                                            })(
                                                <Input placeholder="" />
                                            )}
                                        </Form.Item>}
                                        <Form.Item label="">
                                            {getFieldDecorator('customerName', {
                                                rules: [{ required: true, message: '' }],
                                                initialValue: formData.customerName || ''
                                            })(
                                                <Input readOnly={isReview} placeholder="" />
                                            )}
                                        </Form.Item>
                                        <Form.Item label="">
                                            {getFieldDecorator('customerContacts', {
                                                rules: [{ required: true, message: '' }],
                                                initialValue: formData.customerContacts || ''
                                            })(
                                                <Input readOnly={isReview} placeholder="" />
                                            )}
                                        </Form.Item>
                                        <Form.Item label="">
                                            {getFieldDecorator('contactPost', {
                                                rules: [{ required: false, message: '' }],
                                                initialValue: formData.contactPost || ''
                                            })(
                                                <Input readOnly={isReview} placeholder="" />
                                            )}
                                        </Form.Item>
                                        <Form.Item label="">
                                            {getFieldDecorator('customerPhone', {
                                                rules: [{ required: false, message: '' }],
                                                initialValue: formData.customerPhone || ''
                                            })(
                                                <Input readOnly={isReview} placeholder="" />
                                            )}
                                        </Form.Item>
                                        <Form.Item label="">
                                            {getFieldDecorator('customerEmails', {
                                                rules: [{ required: false, message: '' }],
                                                initialValue: formData.customerEmails || ''
                                            })(
                                                <Input readOnly={isReview} placeholder="" />
                                            )}
                                        </Form.Item>
                                        <Form.Item label="">
                                            {getFieldDecorator('customerAddress', {
                                                rules: [{ required: false, message: '' }],
                                                initialValue: formData.customerAddress || ''
                                            })(
                                                <Input readOnly={isReview} placeholder="" />
                                            )}
                                        </Form.Item>
                                        <Form.Item label="">
                                            {getFieldDecorator('description', {
                                                rules: [{ required: false, message: '' }],
                                                initialValue: formData.description || ''
                                            })(
                                                <Input readOnly={isReview} placeholder="" />
                                            )}
                                        </Form.Item>
                                        <Form.Item label="">
                                            {getFieldDecorator('customerStatus', {
                                                rules: [{ required: false, message: '' }],
                                                initialValue: formData.customerStatus || 0
                                            })(
                                                <Radio.Group disabled={isReview}>
                                                    <Radio value={1}></Radio>
                                                    <Radio value={0}></Radio>
                                                </Radio.Group>
                                            )}
                                        </Form.Item>
                                    </div>
                                }
                            </Form>
                        </div>
                    }
                />
            }
        </div>;
    }
    //
    handleSubmit = e => {
        e.preventDefault();
        const { getDeptData, modalChange, addNew, formData, isReview, columnsType, addNewCustomer } = this.props.SystemStore
        this.props.form.validateFields((err, values) => {
            if (!err) {
                if (columnsType === '1') {
                    if (formData.id) {
                        values.parentId = values.parentId === '' ? 0 : values.parentId
                        addNew(0, { ...formData, ...values }).then(res => {
                            getDeptData(columnsType)
                            message.success('')
                            modalChange(0)
                        })
                    } else {
                        values.parentId = values.parentId === '' ? 0 : values.parentId
                        addNew(1, values).then(res => {
                            getDeptData(columnsType)
                            message.success('')
                            modalChange(0)
                        })
                    }
                } else {
                    if (isReview) {
                        this.props.SystemStore.isReview = false
                        modalChange(0)
                        return
                    }
                    if (formData.id) {
                        addNewCustomer(0, { ...formData, ...values }).then(res => {
                            getDeptData(columnsType)
                            message.success('')
                            modalChange(0)
                        })
                    } else {
                        addNewCustomer(1, { ...values }).then(res => {
                            getDeptData(columnsType)
                            message.success('')
                            modalChange(0)
                        })
                    }
                }
            }
        });
    };
}
const DepartmentForm = Form.create({ name: 'define' })(Department);
export default DepartmentForm;