import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';
import { Divider, Modal, message, Badge, Table } from 'antd';
import { toJS } from 'mobx';


@inject('SystemStore')
@observer
class DepartmentTable extends Component {
    render() {
        const { tableData, isLoading } = toJS(this.props.SystemStore)
        return <div>
            <Table rowKey="id" size='middle' autoExpandParent={false} loading={isLoading} columns={this.columns()} dataSource={tableData} />
        </div>;
    }
    delete = (record) => {
        const { deleteCol, getDeptData, columnsType, deleteCus } = this.props.SystemStore
        Modal.confirm({
            title: '',
            content: '',
            okText: '',
            cancelText: '',
            onOk: () => {
                if (columnsType === '1') {
                    deleteCol({ id: record.id }).then(res => {
                        message.success('')
                        getDeptData(columnsType)
                    })
                } else {
                    deleteCus({ id: record.id }).then(res => {
                        message.success('')
                        getDeptData(columnsType)
                    })
                }

            },
        });

    }
    addLower = (record) => {
        let obj = { parentId: record.id, delFlag: 1 }
        this.props.SystemStore.modalChange(1, obj)
    }
    componentDidMount() {
        const { getDeptData } = this.props.SystemStore
        getDeptData('1')
    }
    columns = () => {
        console.log(this.props.SystemStore.columnsType)
        if (this.props.SystemStore.columnsType === '1') {
            return [
                {
                    title: '',
                    dataIndex: 'name',
                    key: 'name',
                },
                {
                    title: '',
                    dataIndex: 'delFlag',
                    key: 'delFlag',
                    render: (text, record) => (
                        <div>
                            {text ? <Badge status="success" text="" />
                                : <Badge status="error" text="" />}
                        </div>
                    )
                },
                {
                    title: '',
                    key: 'x',
                    align: 'right',
                    render: (text, record, index) => (
                        <span>
                            <a onClick={e => this.props.SystemStore.modalChange(1, record)}></a>
                            <Divider type="vertical" />
                            <a onClick={e => this.addLower(record)}></a>
                            <Divider type="vertical" />
                            <a onClick={e => this.changeStatus(index, record)}>{record.delFlag ? '' : ''}</a>
                            <Divider type="vertical" />
                            <a onClick={e => this.delete(record)}></a>
                        </span>
                    )

                }
            ]
        } else {
            return [
                // {
                //     title: '',
                //     dataIndex: 'customerCode',
                //     key: 'customerCode',
                // },
                {
                    title: '',
                    dataIndex: 'customerName',
                    key: 'customerName',
                },
                {
                    title: '',
                    dataIndex: 'customerAddress',
                    key: 'customerAddress',
                    render: (text) => (
                        <div>{text ? text : '-'}</div>
                    )
                },
                {
                    title: '',
                    dataIndex: 'customerContacts',
                    key: 'customerContacts',
                    render: (text) => (
                        <div>{text ? text : '-'}</div>
                    )
                },
                // {
                //     title: '',
                //     dataIndex: 'contactPost',
                //     key: 'contactPost',
                //     render: (text) => (
                //         <div>{text ? text : '-'}</div>
                //     )
                // },
                {
                    title: '',
                    dataIndex: 'customerPhone',
                    key: 'customerPhone',
                    render: (text) => (
                        <div>{text ? text : '-'}</div>
                    )
                },
                // {
                //     title: '',
                //     dataIndex: 'customerEmails',
                //     key: 'customerEmails',
                //     render: (text) => (
                //         <div>{text ? text : '-'}</div>
                //     )
                // },
                // {
                //     title: '',
                //     dataIndex: 'description',
                //     key: 'description',
                // },
                {
                    title: '',
                    dataIndex: 'customerStatus',
                    key: 'customerStatus',
                    render: (text, record) => (
                        <div>
                            {text ? <Badge status="success" text="" />
                                : <Badge status="error" text="" />}
                        </div>
                    )
                },
                {
                    title: '',
                    key: 'x',
                    align: 'right',
                    render: (text, record) => (
                        <span>
                            <a onClick={e => this.reviewDetail(record)}></a>
                            <Divider type="vertical" />
                            <a onClick={e => this.props.SystemStore.modalChange(1, record)}></a>
                            <Divider type="vertical" />
                            <a onClick={e => this.delete(record)}></a>
                        </span>
                    )
                }
            ]
        }
    }
    reviewDetail = (record) => {
        this.props.SystemStore.isReview = true
        this.props.SystemStore.modalChange(1, record)
    }
    changeStatus = (index, record) => {
        this.props.SystemStore.addNew(0, { ...record, delFlag: record.delFlag ? 0 : 1 }).then(res => {
            if (res) {
                message.success(record.delFlag ? '' : '')
                const { getDeptData, columnsType, customerName } = this.props.SystemStore
                getDeptData(columnsType, { customerName })
            }
        })
    }
}
export default DepartmentTable;